<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入消息内容" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="messageType" label="消息类型" :formatter="formatType">
                    </el-table-column>
                    <el-table-column align="center" prop="messageContent" label="消息内容">
                    </el-table-column>
                    <el-table-column align="center" prop="isRead" label="状态" width="80" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" prop="createAt.time" label="时间" width="150" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="160">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" :page-size="pageSize" @current-change="handleCurrentChange" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="dialog-info" title="查看" :visible.sync="editDialog" @close="getList(word)">
            <div class="info-content">
                <p class="news-content">{{form.adminMessage.messageContent}}</p>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            // 数据
            tableData: [],
            form: {
                adminMessage: ''
            },
            // 弹出框-编辑
            editDialog: false, 
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    methods: {
        // 搜索
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word)
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        //格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.isRead == 1 ? '未读' : row.isRead == 2 ? '已读' : '未知'
            },
            // 格式化类型
            formatType(row, column) {
                return row.messageType == 1 ? '提现申请' : row.messageType == 2 ? '上传车辆' : row.messageType == 2 ? '个人资料' : '--'
            },
        // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "message_id": row.id
                }
            }
            this.$common._post('adminmessage/detail', json, this, (data) => {
                this.form.adminMessage = data.adminMessage
                this.editDialog = true
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "message_id": row.id
                }
            }
            this.$common.deleteItem(this.API + 'adminmessage/delete', this, json)
        },
        // 导出Exc表格
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelAdminMessage', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val
            this.getList(this.word)
        },
        // 获取列表
        getList(messageContent = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "messageContent": messageContent,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('adminmessage/list', json, this, data => {
                this.total = data.total
                this.tableData = data.list
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">
.news-content {
    line-height: 28px;
    font-size: 16px;
    text-indent: 2em;
}
</style>



